import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { Helmet } from 'react-helmet';

const render = C => {
  ReactDOM.render(
    <>
      <Helmet>
        <title></title>
      </Helmet>
      <C />
    </>,
    document.getElementById('root-app'),
  );
};

render(App);
